<template>
  <div class="row object text-center card" v-if="imgsrc">
    <b-img-lazy blank-src="null" :src="imgsrc" class="cl-6" />
    <p class="cl-6">{{ desc }}</p>
    <a :href="link" target="_blank" class="btn btn-primary">View on Harvard site</a>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ObjectCard',
  data() {
    return {
      imgsrc: '',
      desc: '',
      link: '',
    };
  },
  props: ['recordid'],
  beforeMount() {
    this.isLoading = true;
    axios
      .get(
        `https://api.harvardartmuseums.org/object/${this.recordid}?apikey=d8587485-6f9d-4ae7-aacb-56c84bf8b1f4`,
      )
      .then((response) => {
        this.imgsrc = response.data.images[0].baseimageurl;
        this.desc = response.data.title;
        this.link = response.data.url;
      });
  },
};
</script>

