<template>
  <div class="text-center navigation">
    <nav aria-label="Page navigation">
      <ul class="pagination ">
        <li class="page-item" v-if="previous">
          <router-link
            class="page-link"
            :to="previouspage"
            @click="$emit('update', this.previouspage)"
            aria-label="Previous"
          >
            <span aria-hidden="true">Previous</span>
            <span class="sr-only">Previous</span>
          </router-link>
        </li>
        <li class="page-item" v-if="next">
          <router-link class="page-link" :to="nextpage" aria-label="Next">
            <span aria-hidden="true">Next</span>
            <span class="sr-only">Next</span>
          </router-link>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'Pagination',
  data() {
    return {
      next: false,
      previous: false,
      currentpage: this.page,
      totalpages: this.pages,
      nextpage: '',
      previouspage: '',
    };
  },
  props: ['page', 'pages'],
  beforeMount() {
    if (this.currentpage !== this.totalpages) {
      this.next = true;
      this.nextpage = `${Number(this.currentpage) + 1}`;
    }
    if (Number(this.currentpage) !== 1) {
      this.previous = true;
      this.previouspage = `${this.currentpage - 1}`;
    }
  },
};
</script>

<style scoped>
.navigation {
  margin: auto;
}
.page-item {
  margin: auto;
}
</style>
