<template>
  <div>
    <div class="row">
      <div class="cl-6 content card">
        <p>{{ description }}</p>
      </div>
      <div class="cl-6 banner text-center">
        <img :src="imgURL" />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Info',
  data() {
    return {
      imgURL: '',
      description: '',
    };
  },
  mounted() {
    axios
      .get(
        'https://api.harvardartmuseums.org/exhibition?apikey=d8587485-6f9d-4ae7-aacb-56c84bf8b1f4&q=5909',
      )
      .then((response) => {
        this.imgURL = response.data.records[0].poster.imageurl;
        this.description = response.data.records[0].textiledescription.substring(1, 868);
      });
  },
};
</script>

