<template>
  <div :key="this.page">
    <h2 class="text-center">These are artifacts of the Edo period.</h2>
    <div class="row">
      <div v-for="(record, index) in objectId" :key="index" class="cl-4 objectcard">
        <div class="card cl-4 loading" v-if="isLoading">
          <img src="../assets/807.gif" />
        </div>
        <ObjectCard :recordid="record" v-if="!isLoading" />
      </div>
    </div>
    <b-navbar variant="dark" class="bnavbar" v-if="!isLoading">
      <Pagination :page="page" :pages="pages" />
    </b-navbar>
  </div>
</template>

<script>
import axios from 'axios';
import ObjectCard from '@/components/ObjectCard.vue';
import Pagination from '@/components/Pagination.vue';

export default {
  name: 'Images',
  data() {
    return {
      objectId: [],
      page: this.$route.params.page,
      pages: '',
      isLoading: false,
    };
  },
  beforeMount() {
    this.isLoading = true;
    axios
      .get(
        `https://api.harvardartmuseums.org/object?apikey=d8587485-6f9d-4ae7-aacb-56c84bf8b1f4&period=248&page=${this.page}`,
      )
      .then((response) => {
        response.data.records.forEach((record) => {
          this.objectId.push(record.id);
        });
        this.pages = response.data.info.pages;
        setTimeout(() => {
          this.isLoading = false;
        }, 2500);
      });
  },
  components: {
    ObjectCard,
    Pagination,
  },
};
</script>

<style scoped>
.objectcard {
  margin-left: 3rem;
}
h2 {
  text-decoration: underline;
}
.bnavbar {
  width: 12rem;
  margin: auto;
  border-radius: 7px 7px 0 0;
}
.loading {
  margin: 3rem 5rem;
  border-radius: 7px;
}
</style>
